import { Component, OnInit } from '@angular/core';
import { NodeJsService } from '../nodejs.service';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.css']
})
export class SignUpComponent implements OnInit {
formSignUp: FormGroup;

  constructor(private formBuilder: FormBuilder, private nodeJsService: NodeJsService) {
    // this.formSignUp = new FormGroup({
    //     email: new FormControl('bambi@yahoo.com'),
    //     password: new FormControl(),
    //     subjects: new FormGroup({
    //         angular: new FormControl(false),
    //         react: new FormControl(true),
    //         nodejs: new FormControl(false)
    //     })
    // });
  }

  ngOnInit() {
    this.formSignUp = this.formBuilder.group({
      email: ['', [gmailValidator, Validators.email]],
      password: '',
      subjects: this.formBuilder.group({
          angular: false,
          react: true,
          nodejs: false
      })
    });
  }

  onSubmit() {
    console.log(this.formSignUp.value);
  }
}

function gmailValidator(formControl: FormControl) {
    return formControl.value.includes('@gmail.com') ? null : { gmail: true };
}



