import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-struct',
  templateUrl: './struct.component.html',
  styleUrls: ['./struct.component.css']
})
export class StructComponent implements OnInit {
  isShow = true;
  isShowForm = false;
  newEn= '';
  newVn= '';
  filterStatus= 'XEM_TAT_CA';
 arrWords = [
    { id: 1, en: 'action', vn: 'hành động', memorized: true },
    { id: 2, en: 'actor', vn: 'diễn viên', memorized: false },
    { id: 3, en: 'activity', vn: 'hoạt động', memorized: true },
    { id: 4, en: 'active', vn: 'chủ động', memorized: true },
    { id: 5, en: 'bath', vn: 'tắm', memorized: false },
    { id: 6, en: 'bedroom', vn: 'phòng ngủ', memorized: true }
];


  arrSubjects = ['Angular', 'NodeJs', 'React'];
  constructor() { }

  ngOnInit() {
  }

  addWord() {
    this.arrWords.unshift( {
        id: this.arrWords.length + 1,
        en: this.newEn,
        vn: this.newVn,
        memorized: false
    });

    this.newEn = '';
    this.newVn = '';
    this.isShowForm = false;
  }

  deleteWord(id) {
    const index = this.arrWords.findIndex(word => word.id === id);
    this.arrWords.splice(index, 1);
  }

  memorizeWord(word) {
    word.memorized = !word.memorized;
  }

  getVisibility(memorized) {
    console.log(this.filterStatus === 'XEM_TAT_CA' ||
    (this.filterStatus === 'XEM_DA_NHO' && memorized) ||
    (this.filterStatus === 'XEM_CHUA_NHO' && !memorized));
    console.log('abc: ' + memorized);
    console.log('bcd: ' + this.filterStatus);
    return this.filterStatus === 'XEM_TAT_CA' ||
    (this.filterStatus === 'XEM_DA_NHO' && memorized) ||
    (this.filterStatus === 'XEM_CHUA_NHO' && !memorized);
  }
}



